.gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.photo {
  width: 300px;
  height: 250px;
  // overflow: hidden;
}

.photo-link {
  text-decoration: none;
  border-radius: 0px 0px 4px 4px;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
    0px 2px 1px rgb(0 0 0 / 20%);
  color: #fff;
}

.info {
  padding: 10px 10px;
  display: flex;
  justify-content: space-around;
  // align-items: center;
  gap: 10px;
  background-color: rgb(56, 2, 56);
  color: white;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
}

.info-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
